* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: "Pier Sans";
  src: url("../fonts/PierSans-Light.otf") format("opentype"),
    url("../fonts/PierSans-Medium.otf") format("opentype");
}

body {
  font-family: "Pier Sans";
}

canvas {
  display: block;
}

.slidecontainer {
  width: 100%;
}
.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 2px;
  background: #000;
  outline: none;
  opacity: 0.9;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
  border-radius: 1px;
}
.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  border-radius: 50%;
  appearance: none;
  width: 20px;
  height: 20px;
  background: #fff;
  cursor: pointer;
  filter: drop-shadow(-1px 1px 5px rgba(0, 0, 0, 0.3));
  transition: ease-in-out 100ms background;

  &:hover {
    background: #eeeeee;
  }
}
.slider::-moz-range-thumb {
  -webkit-appearance: none;
  border-radius: 50%;
  appearance: none;
  width: 20px;
  height: 20px;
  background: #fff;
  filter: drop-shadow(0px 0px 11px rgba(0, 0, 0, 0.5));
  cursor: pointer;
  transition: ease-in-out 100ms background;

  &:hover {
    background: #eeeeee;
  }
}

.controls {
  display: flex;
  visibility: hidden;
  flex-direction: column;
  width: 23rem;
  position: absolute;
  top: 0;
  background: rgba(255, 255, 255, 0.7);
  color: black;
  border-radius: 2px;
  backdrop-filter: blur(5px);
  padding: 20px;

  input {
    margin-bottom: 20px;
    margin-top: -10px;
  }

  .heading {
    display: flex;

    button {
      background: transparent;
      margin-left: 100px;
      font-size: 2rem;
      margin-top: 28px;
      outline: none;
      height: 50px;
      width: 50px;
      border: none;
      align-content: space-between;
      border-radius: 100%;
      transition: ease-in-out 150ms filter;
      &:hover {
        filter: opacity(0.6);
      }
    }
  }

  h1 {
    padding: 0;
    font-weight: 600;
    font-size: 5rem;
    display: flex;
    flex-direction: column;
    margin-bottom: 1.5rem;
    span {
      margin-top: -5px;
      font-weight: 100;
      font-size: 1.1rem;
      opacity: 0.5;
    }
  }

  h2 {
    font-size: 2rem;
    font-weight: 100;

    margin-bottom: 1rem;
  }
}

.menuOpen {
  position: absolute;
  top: 1rem;
  left: 1rem;
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(5px);
  border-radius: 50px;
  padding: 18px;
  cursor: pointer;
  border: none;
  outline: none;

  p {
    font-size: 2rem;
  }

  transition: ease-in 100ms background;

  &:hover {
    background: rgba(255, 255, 255, 0.4);
  }
}

.anchor {
  margin-top: 10px;
  color: #273c75;

  text-decoration: underline;

  transition: ease-in-out 100ms color;

  &:hover {
    color: #000;
  }

  width: 30%;
}

#resetBtn {
  position: absolute;
  right: 5%;
  bottom: 4.3%;
  font-size: 1rem;
  border: none;
  padding: 6px 20px;
  border-radius: 2px;
  background-color: #3498db;
  color: white;
  outline: none;

  transition: ease-in-out 100ms filter;

  &:hover {
    filter: brightness(0.9);
  }
}
